.page-calculation__request {
  // border: $bred;
  display: flex;
  max-width: 770px;
  margin: 67px auto 0;
  flex-wrap: wrap;
  padding: 73px 70px 70px;
  background: #F6F7FF;

  @media (max-width: 1023px) {
    width: 100%;
    background: none;
    padding: 60px 45px 0;
    margin: 0 auto;
  }
  @media (max-width: 767px) {
    padding: 48px 15px 0;
  }

  h3 {
    width: 100%;
    // border: $bred;
    margin: 0;
    text-align: center;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 70%;
    margin-bottom: auto;
    @media (max-width: 1023px) {
      font-size: 18px;
    }
    @media (max-width: 767px) {
      font-size: 16px;
    }
  }
}

.page-calculation__request-information {
  width: 100%;
  // border: $bred;
  display: flex;
  flex-direction: column;
  justify-content: start;
  margin-top: 22px;

  @media (max-width: 1023px) {
    margin-top: 35px;
  }

  @media (max-width: 767px) {
    margin-top: 25px;
  }
}

.page-calculation__request--line {
  width: 100%;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: #C4C4C4;
  display: flex;
  height: 55px;
  align-items: center;
  justify-content: flex-end;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 140%;

  @media (max-width: 767px) {
    // border: $bred;
    font-size: 18px;
    flex-direction: column-reverse;
    align-items: flex-start;
    justify-content: flex-start;
    padding-bottom: 12px;
    height: 76px;
  }
}

.request-article {
margin-right: auto;
font-family: Roboto;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 140%;
}


.page-calculation__form {
  width: 100%;
  // border: $bred;
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;

  input {
    text-align: start;
    padding-left: 20px;
  }

  .field--name__input,
  .field--phone__input,
  .field--email__input {
    width: 100%;
    height: 60px;
    margin-bottom: 20px;

    @media (max-width: 767px) {
      margin-bottom: 10px;
    }
  }

  .field--phone__input,
  .field--email__input {
    margin-top: 10px;
    width: 48%;

    @media (max-width: 767px) {
      width: 100%;
    }
  }

  .field--email__input {
    margin-left: auto;
  }

  .calculation__send-btn {
    width: 198px;
    height: 51px;
    margin: 20px auto 0;
    background: #2C36F2;
    border-radius: 4px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 70%;
    color: #F6F7FF;

    &:active {
      box-shadow: inset 0 0 0 1px #1c05a1, inset 0 5px 40px #9fb5f1;
    }

    @media (max-width: 1023px) {
      margin: 19px auto 0;
      width: 100%;
    }

    @media (max-width: 767px) {
      font-size: 14px;
    }
  }
}
